.progectBlock {
    min-height: 100vh;
    background-color: #c98cfc;
}

.progectsContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
}

.title {
    background-color: aliceblue;
}

.progects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}